.main-header {
  margin-bottom: 31px;
  background-color: $color-background-main-header;

  @media ($tablet-width) {
    margin-bottom: 55px;
  }

  @media ($desktop-width) {
    margin-bottom: 54px;
  }
}

.main-header__wrapper {
  @media ($mobile-width) {
    background-image: url("../img/bg-header-mobile.jpg");
    background-repeat: no-repeat;
    background-position: center top;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bg-header-mobile@2x.jpg");
      background-size: 320px 373px;
    }
  }

  @media ($tablet-width) {
    position: relative;
    background-image: url("../img/bg-header-tablet.jpg");
    background-repeat: no-repeat;
    background-position: center -48px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/bg-header-tablet@2x.jpg");
      background-size: 768px 746px;
    }

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 24px;
      left: 0;
      bottom: -15px;
      background-image: url("../img/zigzag-header-tablet.svg");
      background-repeat: repeat-x;
    }
  }

  @media ($desktop-width) {
    position: relative;

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 34px;
      left: 0;
      bottom: -20px;
      background-image: url("../img/zigzag-main-header-desktop.svg");
      background-repeat: repeat-x;
    }
  }
}

.main-header__title {
  margin: 0 auto;
  min-height: 283px;
  padding-top: 73px;
  padding-bottom: 120px;

  @media ($tablet-width) {
    padding-top: 110px;
    padding-bottom: 372px;
  }

  @media ($desktop-width) {
    padding-top: 113px;
    padding-bottom: 355px;
  }
}

.main-header__list {
  margin: 0 auto;
  padding: 0;

  @media ($tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 900px;
    padding: 0 50px;
  }

  @media ($desktop-width) {
    max-width: 1150px;
  }
}

.main-header__link {
  display: block;
  position: relative;
  font-family: inherit;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: $color-basic-white;
  text-decoration: none;
  background-color: $color-accent-green;
  padding: 25px 100px 30px 30px;

  @media ($mobile-width) {
    span {
      display: block;
      max-width: 500px;
      margin: 0 auto;
    }
  }

  @media ($tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-left: 36px;
  }
}

.main-header__link--interior {
  &::after {
    position: absolute;
    content: "";
    width: 41px;
    height: 62px;
    right: 35px;
    top: 23px;
    opacity: 0.5;
    background-image: url("../img/main-header_interior.svg");
    background-repeat: no-repeat;

    @media ($tablet-width) {
      top: 29px;
    }
  }

  &:hover {
    margin-top: -5px;
    padding-bottom: 35px;

    &::after {
      opacity: 0.7;
    }
  }

  &:focus {
    outline: 1px solid $color-basic-black;
  }

  &:active {
    &::after {
      opacity: 0.3;
    }
  }
}

.main-header__link--kidtoys {
  background-color: $color-kidtoys-background;

  &::after {
    position: absolute;
    content: "";
    width: 60px;
    height: 60px;
    right: 27px;
    top: 21px;
    opacity: 0.5;
    background-image: url("../img/main-header_toy.svg");
    background-repeat: no-repeat;

    @media ($tablet-width) {
      right: 34px;
      top: 31px;
    }
  }

  &:hover {
    margin-top: -5px;
    padding-bottom: 35px;

    &::after {
      opacity: 0.7;
    }
  }

  &:focus {
    outline: 1px solid $color-basic-black;
  }

  &:active {
    &::after {
      opacity: 0.3;
    }
  }
}
